import React from 'react'
import { Drizzle, generateStore } from 'drizzle'
import Context from './Context'
import Mycelium from '../contracts/Mycelium.json'
console.log(Drizzle)
// const RINKEBY_WSS = 'wss://rinkeby.infura.io/ws'
// var provider = new Web3.providers.WebsocketProvider(RINKEBY_WSS)
// var web3 = new Web3(provider)

const options = {
  contracts: [Mycelium],
  web3: {
    fallback: {
      type: 'ws',
      url: 'wss://ropsten.infura.io/ws',
    },
  },
}

const drizzleStore = generateStore(options)
const drizzle = new Drizzle(options, drizzleStore)
console.log(drizzle)
const Provider = ({ children }) => {
  return <Context.Provider drizzle={drizzle}>{children}</Context.Provider>
}

export default Provider
